<template>
  <!-- eslint-disable max-len -->
  <div>
    <b-table striped hoverable :data="table_data" :columns="columns" />
  </div>
</template>

<script>
export default {
  name: 'FhirObservations',
  props: {
    observations: Array,
  },
  computed: {
    columns() {
      return [
        {
          field: 'observation', label: 'Bepaling', searchable: true, sortable: true,
        },
        {
          field: 'value', label: 'Waarde', searchable: true, sortable: true,
        },
        {
          field: 'date', label: 'Datum', searchable: true, sortable: true,
        },
        {
          field: 'zkh', label: 'ZKH', searchable: true, sortable: true,
        },
      ];
    },
    table_data() {
      return this.observations.map((obs) => ({
        category: obs.resource.category[0].coding[0].display,
        observation: obs.resource.code.text,
        value: this.value(obs),
        date: new Date(obs.resource.effectiveDateTime).toLocaleString(),
        zkh: obs.zkh,
      }));
    },
  },

  methods: {
    value(observation) {
      if (observation.resource.valueQuantity) {
        return `${observation.resource.valueQuantity.value} ${observation.resource.valueQuantity.unit}`;
      }
      if (observation.resource.valueCodeableConcept) {
        return observation.resource.valueCodeableConcept.text;
      }
      if (observation.resource.code.coding[0].code === '85354-9') {
        const [diastolic, systolic] = observation.resource.component;
        return `${systolic.valueQuantity.value}/${diastolic.valueQuantity.value} ${systolic.valueQuantity.unit}`;
      }
      return '--';
    },
  },
};
</script>
